.ai-features-panel {
  position: fixed;
  top: 130px; /* 탭바(38px) + 메뉴바(48px) + 헤더(44px) = 130px */
  right: 50px; /* 우측 메뉴(40px) + 여백(10px) */
  bottom: 0;
  width: 380px; /* 320px에서 380px로 확대 */
  background: var(--bg-primary);
  border-left: 1px solid var(--border-color);
  box-shadow: -2px 0 8px var(--shadow);
  padding: 16px;
  overflow-y: auto;
  z-index: 1000;
  transition: transform 0.25s cubic-bezier(.4, 1.4, .6, 1), opacity 0.25s cubic-bezier(.4, 1.4, .6, 1);
  transform: translateX(0);
  opacity: 1;
}

.ai-features-panel.closed {
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 8px 16px 0; /* 하단 패딩 증가 */
  margin-bottom: 8px;
  border-bottom: 1px solid #f0f0f0; /* 구분선 추가 */
}

.panel-main-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0;
}

.close-panel-btn {
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  width: 24px;
  height: 24px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #888;
  border-radius: 4px;
  transition: background 0.15s;
}

.close-panel-btn:hover {
  background: #f0f0f0;
}

.panel-section {
  margin-bottom: 20px;
}

.panel-title {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 12px;
  color: #333;
}

.setting-group {
  margin-bottom: 12px;
}

.method-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
}

.method-option {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  cursor: pointer;
  transition: border-color 0.15s, background-color 0.15s;
}

.method-option:hover:not(.disabled) {
  border-color: #007AFF;
  background-color: #f8f9ff;
}

.method-option.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: #f5f5f5;
}

.method-option input[type="radio"] {
  margin: 0;
}

.method-label {
  font-size: 14px;
  font-weight: 500;
}

.checking {
  color: #666;
  font-size: 12px;
}

.unavailable {
  color: #ff6b6b;
  font-size: 12px;
}

.available {
  color: #28a745;
  font-size: 12px;
}

.method-info {
  margin-top: 8px;
  padding: 8px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border-left: 3px solid #007AFF;
}

.method-info small {
  color: #666;
  font-size: 12px;
}

.setting-label {
  display: block;
  font-size: 13px;
  color: #555;
  margin-bottom: 6px;
  font-weight: 500;
}

.property-input {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 13px;
  background: var(--bg-primary);
  transition: border-color 0.15s;
}

.property-input:focus {
  outline: none;
  border-color: #007AFF;
  box-shadow: 0 0 0 2px rgba(0, 122, 255, 0.1);
}

.generate-btn {
  width: 100%;
  background: linear-gradient(135deg, #007AFF, #5856D6);
  color: white;
  border: none;
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 8px;
}

.generate-btn:hover:not(:disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 122, 255, 0.3);
}

.generate-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.divider {
  border: none;
  border-top: 1px solid #eee;
  margin: 16px 0;
}

.summary-result {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 16px;
  border: 1px solid #e9ecef;
}

.summary-text {
  font-size: 14px;
  line-height: 1.6;
  color: #333;
  margin-bottom: 16px;
  white-space: pre-wrap;
}

.summary-actions {
  display: flex;
  gap: 8px;
}

.action-btn {
  flex: 1;
  background: var(--bg-primary);
  border: 1px solid #ddd;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.15s;
}

.action-btn:hover {
  background: #f5f5f5;
  border-color: #bbb;
}

.loading-state {
  text-align: center;
  padding: 24px 16px;
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #f3f3f3;
  border-top: 3px solid #007AFF;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 16px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-text {
  font-size: 13px;
  color: #666;
}

.info-box {
  background: linear-gradient(135deg, #f8f9fa, #e9ecef);
  border-radius: 12px;
  padding: 20px;
  text-align: center;
  border: 1px solid #dee2e6;
}

.info-icon {
  font-size: 32px;
  margin-bottom: 12px;
}

.info-text {
  color: #555;
}

.info-text p {
  margin: 0 0 8px 0;
  font-size: 14px;
}

.info-text strong {
  color: #333;
}

.info-text ul {
  list-style: none;
  padding: 0;
  margin: 12px 0 0 0;
  text-align: left;
}

.info-text li {
  font-size: 13px;
  margin-bottom: 4px;
  color: #666;
}

.content-view-btn {
  background: linear-gradient(135deg, #17a2b8, #138496);
  border: none;
  color: white;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 13px;
  font-weight: 500;
  width: 100%;
  margin-top: 8px;
  transition: all 0.2s ease;
  
  &:hover {
    background: linear-gradient(135deg, #138496, #117a8b);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(19, 132, 150, 0.3);
  }
  
  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 4px rgba(19, 132, 150, 0.2);
  }
}

.pdf-content-preview {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.4;
  color: #555;
  max-height: 150px;
  overflow-y: auto;
  white-space: pre-wrap;
  word-break: break-word;
}

.content-stats {
  font-size: 11px;
  color: #888;
  text-align: right;
  margin-bottom: 8px;
}

.no-content-message {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 20px;
  text-align: center;
  color: #6c757d;
  font-size: 13px;
  font-style: italic;
}
